import React, { useState } from 'react'
import { Form, Row, Col, Input, Button } from 'antd'
import { Select } from 'antd'

const { Option } = Select

export default function Search() {
  const [form] = Form.useForm()

  const getFields = () => {
    const children = [
      <Col span={8} key="checkStatus">
        <Form.Item name="checkStatus" label="百度审核">
          <Select allowClear>
            <Option value="0">未提交</Option>
            <Option value="1">待审核</Option>
            <Option value="2">已通过</Option>
            <Option value="3">已拒绝</Option>
          </Select>
        </Form.Item>
      </Col>,
      <Col span={8} key="brandName">
        <Form.Item name="brandName" label="品牌名称">
          <Input placeholder="品牌名称" />
        </Form.Item>
      </Col>,
      <Col span={8} key="id">
        <Form.Item name="id" label="名称或ID">
          <Input placeholder="名称或ID或百度ID" />
        </Form.Item>
      </Col>,
    ]
    return children
  }

  const onFinish = (values) => {
    console.log('Received values of form: ', values)
  }

  return (
    <div className="m-content-search">
      <Form
        form={form}
        name="advanced_search"
        className="ant-advanced-search-form"
        onFinish={onFinish}
      >
        <Row gutter={24}>{getFields()}</Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <Button type="primary" htmlType="submit">
              搜索
            </Button>
            <Button
              style={{ margin: '0 8px' }}
              onClick={() => {
                form.resetFields()
              }}
            >
              重置
            </Button>
          </Col>
        </Row>
      </Form>
    </div>
  )
}
